<template>
  <div>
    <v-container>
      <v-row>
        <v-col
          cols="12"
        >
          <v-card
            class="mx-auto"
            max-width="650"
          >
            <v-form>
              <v-card-title>
                <span class="text-h4 text-md-h3">Relatório de Frota</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row justify="center">
                    <v-col
                      cols="12"
                      sm="6"
                      md="12"
                    >
                      <v-autocomplete
                        v-model="plate"
                        :items="plates"
                        color="blue"
                        class="grey-label"
                        outlined
                        hide-details
                        label="Placa"
                        prepend-icon="mdi-account-outline"
                        item-text="PLACA"
                        item-value="PLACA"
                        @mouseover="plates.length != 0 ? '' : fetchPlates()"
                        onkeyup="this.value = this.value.toUpperCase()"
                      >
                        <template v-slot:no-data>
                          <v-list-item>
                            <v-list-item-title>
                              Nenhum item encontrado
                              <br><br>
                              <v-progress-linear
                                indeterminate
                                color="blue"
                              />
                            </v-list-item-title>
                          </v-list-item>
                        </template>
                      </v-autocomplete>
                    </v-col>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-autocomplete
                        v-model="state"
                        :items="plates"
                        color="blue"
                        class="grey-label"
                        hide-details
                        prepend-icon="mdi-map-marker-outline"
                        label="Estado"
                        placeholder="Selecione"
                        outlined
                        item-text="ESTADO"
                        item-value="ESTADO"
                        @mouseover="plates.length != 0 ? '' : fetchPlates()"
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-select
                        v-model="status"
                        color="blue"
                        class="grey-label"
                        prepend-icon="mdi-list-status"
                        hide-details
                        multiple
                        :items="licenceHeaders"
                        label="Status"
                        placeholder="Selecione"
                        outlined
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                    >
                      <v-text-field
                        v-model="computedDateFormatted1"
                        readonly
                        outlined
                        hide-details
                        max-width="100"
                        label="Início"
                        class="my-0 ms-2 pa-0"
                      />
                      <v-date-picker
                        v-model="date1"
                        color="amber"
                        locale="pt-BR"
                        no-title
                        class="ma-0 pa-0 mx-auto"
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                    >
                      <v-text-field
                        v-model="computedDateFormatted2"
                        readonly
                        outlined
                        hide-details
                        max-width="100"
                        label="Final"
                        class="my-0 ms-2 pa-0"
                      />
                      <v-date-picker
                        v-model="date2"
                        color="amber"
                        locale="pt-BR"
                        no-title
                        class="ma-0 pa-0 mx-auto"
                      />
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions class="mt-n10 px-10 py-5">
                <v-btn
                  fab
                  small
                  :disabled="!licences"
                  :loading="loading2"
                  @click="downloadPDF()"
                >
                  <v-icon
                    dark
                    color="red darken-2"
                  >
                    mdi-file-pdf-box
                  </v-icon>
                </v-btn>
                <v-btn
                  fab
                  small
                  :disabled="!licences"
                  :loading="loading3"
                  @click="downloadXLSX('xlsx')"
                >
                  <v-icon
                    dark
                    color="green darken-2"
                  >
                    mdi-microsoft-excel
                  </v-icon>
                </v-btn>
                <v-spacer />
                <v-btn
                  color="blue darken-3 white--text"
                  :loading="loading"
                  @click="generateReport(), loading = true"
                >
                  Gerar Relatório
                </v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-container v-if="showReport === true">
          <v-data-table
            :headers="headers"
            :items="licences"
            :footer-props="{ itemsPerPageOptions: [5, 10, -1] }"
          />
        </v-container>
      </v-row>
    </v-container>
    <vue-html2pdf
      :show-layout="false"
      :enable-download="true"
      :manual-pagination="false"
      :paginate-elements-by-height="14001"
      :filename="reportName"
      :pdf-quality="2"
      pdf-format="a4"
      pdf-orientation="landscape"
      pdf-content-width="100%"
      @startPagination="loading2 = true"
      @hasDownloaded="loading2 = false"
      ref="html2Pdf"
    >
      <section slot="pdf-content">
        <v-container v-if="showReport === true">
          <v-data-table
            :headers="headers"
            :items="licences"
            :footer-props="{ itemsPerPageOptions: [-1] }"
          />
        </v-container>
      </section>
    </vue-html2pdf>
    <table
      v-show="false"
      ref="exportableTable"
    >
      <tr>
        <th
          v-for="(header, idx) in headers"
          :key="'header' + idx"
        >
          {{ header.text }}
        </th>
      </tr>
      <tr
        v-for="(licence, idx) in licences"
        :key="'data' + idx"
      >
        <td>{{ usDate(licence.DATA) }}</td>
        <td>{{ licence.SOLICITANTE }}</td>
        <td>{{ licence.EMPRESA }}</td>
        <td>{{ licence.ESTADO }}</td>
        <td>{{ licence.PLACA }}</td>
        <td>{{ licence.NUMTAXA }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
  import axios from 'axios'
  import VueHtml2pdf from 'vue-html2pdf'
  import * as XLSX from 'xlsx/xlsx.mjs'

  export default {
    name: 'FleetReport',

    components: {
      VueHtml2pdf
    },

    data: () => ({
      company: '',
      state: '',
      plate: '',
      status: '',
      table: '',
      dateFormatted: '',
      reportName: '',
      showReport: false,
      licences: null,
      loading: false,
      loading2: false,
      loading3: false,
      date1: new Date(new Date().getFullYear(), new Date().getMonth(), 1).toISOString().substr(0, 10), // first day of the month
      date2: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10), // today
      companies: [],
      plates: [],
      states: [],
      headers: [
        { text: 'ID', value: 'ID' },
        { text: 'Placa', value: 'PLACA' },
        { text: 'UF', value: 'ESTADO' },
        { text: 'AET', value: 'AET' },
        { text: 'Emissão', value: 'EMISSÃO' },
        { text: 'Vencimento', value: 'VENCIMENTO' },
        { text: 'Status', value: 'STATUSAET' },
      ],
      licenceHeaders: [
        { text: 'Vencida', value: 'VENCIDA' },
        { text: 'Válida', value: 'VALIDA' },
        { text: 'Vencimento próximo', value: 'Vence em' },
      ]
    }),

    computed: {
      razaoSocial () {
        return window.sessionStorage.razaosocial
      },
      computedDateFormatted1 () {
        return this.formatDate(this.date1)
      },
      computedDateFormatted2 () {
        return this.formatDate(this.date2)
      },
    },

    watch: {
      date1 () {
        this.dateFormatted = this.formatDate(this.date1)
      },
      date2 () {
        this.dateFormatted = this.formatDate(this.date2)
      },
      licences () {
        return this.reportName = 'Relatório NVS ' + this.date2
      },
    },

    created() {
      // this.fetchCompanies()
    },

    methods: {
      // fetchCompanies () {
      //   return fetch('https://sheetdb.io/api/v1/afxq0nl1f0gvg')
      //     .then((r) => r.json())
      //     .then((r) => {
      //       this.companies = r
      //     })
      // },
      fetchPlates () {
        fetch('https://sheetdb.io/api/v1/afxq0nl1f0gvg')
          .then((r) => r.json())
          .then((r) => {
            this.companies = r
          }
        ).then(() => {
            let plate = this.companies.filter(item => (this.razaoSocial === item.EMPRESA))
            this.plates = plate
            console.log(this.plates)
          })
      },
      formatDate (date) {
        if (!date) return null
        var [year, month, day] = date.split('-')
        return `${day.padStart(2, '0')}/${month.padStart(2, '0')}/${year}`
      },
      parseDate (date) {
        if (!date) return null
        const [day, month, year] = date.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
      usDate (date) {
        if (!date) return null
        const [day, month, year] = date.split('/')
        var secs = '00:00:28'
        return `${month.padStart(2, '0')}/${day.padStart(2, '0')}/${year} ${secs}`
      },
      fetchLicences () {
        return axios.get('https://sheetdb.io/api/v1/afxq0nl1f0gvg')
      },
      generateReport () {
        this.fetchLicences().then((r) => {
          var table = r.data

          this.$nextTick(() => {
            var totalResult = ''
            var totalResult1 = ''
            var totalResult2 = ''
            var totalResult3 = ''
            totalResult = table.filter(item =>
              item.EMPRESA.includes(this.company) &&
              item.PLACA.includes(this.plate) &&
              item.ESTADO.includes(this.state) &&
              item.STATUSAET.includes(this.status) &&
              this.parseDate(item.DATA).replace(/-/g, '') >= this.date1.replace(/-/g, '') &&
              this.parseDate(item.DATA).replace(/-/g, '') <= this.date2.replace(/-/g, '')
            )
            totalResult1 = table.filter(item =>
              item.EMPRESA.includes(this.company) &&
              item.PLACA.includes(this.plate) &&
              item.ESTADO.includes(this.state) &&
              item.STATUSAET.includes(this.status[0]) &&
              this.parseDate(item.DATA).replace(/-/g, '') >= this.date1.replace(/-/g, '') &&
              this.parseDate(item.DATA).replace(/-/g, '') <= this.date2.replace(/-/g, '')
            )
            totalResult2 = table.filter(item =>
              item.EMPRESA.includes(this.company) &&
              item.PLACA.includes(this.plate) &&
              item.ESTADO.includes(this.state) &&
              item.STATUSAET.includes(this.status[1]) &&
              this.parseDate(item.DATA).replace(/-/g, '') >= this.date1.replace(/-/g, '') &&
              this.parseDate(item.DATA).replace(/-/g, '') <= this.date2.replace(/-/g, '')
            )
            totalResult3 = table.filter(item =>
              item.EMPRESA.includes(this.company) &&
              item.PLACA.includes(this.plate) &&
              item.ESTADO.includes(this.state) &&
              item.STATUSAET.includes(this.status[2]) &&
              this.parseDate(item.DATA).replace(/-/g, '') >= this.date1.replace(/-/g, '') &&
              this.parseDate(item.DATA).replace(/-/g, '') <= this.date2.replace(/-/g, '')
            )

            this.status.length === 0 ? this.licences = totalResult : this.licences = totalResult1.concat(totalResult2).concat(totalResult3)

            this.showReport = true
            this.loading = false
          })
        })
      },
      downloadPDF () {
        this.$refs.html2Pdf.generatePdf()
      },
      downloadXLSX (type, fn, dl) {
        var el = this.$refs.exportableTable
        var wb = XLSX.utils.table_to_book(el, {sheet:'Relatório'})
        this.loading3 = true
        setTimeout(() => {
          this.loading3 = false
          return dl ?
            XLSX.write(wb, {bookType: type, bookSTT: true, type: 'string'}) :
            XLSX.writeFile(wb, fn || (this.reportName + '.' +  (type || 'xlsx')))
        }, 100)
      },
    },
  }
</script>
